.container {
    display: flex;
    flex-direction: column;
    gap: 10px;
    width: 100%;
    height: 500px;
    padding: 10px;
    background-color: #ffffff;
    border-radius: 8px;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
    animation: fadeIn 0.3s ease-out;
}

