/* Styles for MonthDays component */
.monthdays {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: linear-gradient(to top, #f0f4f8, #d9e2ec);
    border-top: 1px solid rgba(255, 165, 0, 0.3);
    color: #2c3e50;
    font-weight: 300;
    font-size: 0.85rem;
    padding: 0.2rem;
    text-align: center;
    border: 1px solid rgba(160, 210, 235, 0.5);
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    height: 3.5vh;
    min-height: 40px;
}

.today {
    background: linear-gradient(145deg, #052fee, #207ef8) !important;
    box-shadow: 0 0 15px rgba(255, 204, 203, 0.4);
    position: relative;
    color: wheat;
}