.description-container {
    position: fixed;
    background-color: rgba(255, 255, 255, 0.95);
    border-radius: 12px;
    box-shadow: 0 15px 40px rgba(0, 0, 0, 0.2);
    padding: 10px;
    width: 100%;
    max-width: 500px;
    height: auto;
    min-height: 320px;
    display: flex;
    flex-direction: column;
    z-index: 1000000;
    animation: fadeIn 0.3s ease-out;
}

.buttons-container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 8px;
    padding: 8px;
    flex-shrink: 0;
}

.button-wrapper {
    position: relative;
}

.menu-button {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 6px;
    padding: 8px;
    background-color: #f8fafc;
    border: 1px solid #e2e8f0;
    border-radius: 8px;
    font-size: 12px;
    color: #475569;
    cursor: pointer;
    transition: all 0.2s;
    width: 100%;
}

.menu-button:hover {
    background-color: #f1f5f9;
    transform: translateY(-1px);
}

.menu-button.active {
    background-color: #e2e8f0;
    border-color: #cbd5e1;
}

.menu-content {
    position: absolute;
    top: 70px;
    left: 0;
    right: 0;
    min-height: 250px;
    background-color: white;
    border-radius: 8px;
    margin: 8px;
    padding: 15px;
    box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1);
    z-index: 3;
}

.reservation-highlight {
    margin: 8px;
    margin-top: 10px;
    padding: 12px;
    background-color: #f0f7ff;
    border-radius: 8px;
    border-left: 4px solid #2563eb;
    z-index: 2;
}

.status-badge {
    display: inline-flex;
    align-items: center;
    padding: 6px 12px;
    border-radius: 6px;
    font-weight: 600;
    font-size: 0.9em;
}

@keyframes fadeIn {
    from {
        opacity: 0;
        transform: scale(0.95);
    }

    to {
        opacity: 1;
        transform: scale(1);
    }
}