.guest-search-container {
    width: 350px;
    /* Ancho fijo para el contenedor */
    padding: 1rem;
    background-color: #1565c0;
    border-radius: 4px;
    color: white;
}

.guest-item {
    width: 100%;
    padding: 0.75rem;
    background: rgba(255, 255, 255, 0.1);
    border: none;
    border-radius: 4px;
    color: white;
    text-align: left;
    cursor: pointer;
    transition: all 0.2s ease;
    margin-bottom: 0.5rem;
}

.guest-item-content {
    display: grid;
    grid-template-columns: 1fr auto;
    /* Dos columnas: contenido y fecha */
    align-items: center;
    gap: 1rem;
    width: 100%;
}

.guest-item-left {
    display: flex;
    align-items: center;
    gap: 0.75rem;
    min-width: 0;
    /* Permite que el contenido se ajuste */
}

.guest-info {
    display: flex;
    flex-direction: column;
    gap: 0.25rem;
    min-width: 0;
    /* Permite que el texto se ajuste */
    flex: 1;
}

.guest-name {
    font-weight: 500;
    font-size: 0.95rem;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.guest-info-details {
    display: flex;
    gap: 1rem;
    font-size: 0.8rem;
    color: rgba(255, 255, 255, 0.7);
}

.guest-room,
.guest-dni {
    display: flex;
    align-items: center;
    gap: 0.25rem;
    white-space: nowrap;
}

.guest-date {
    font-size: 0.8rem;
    color: rgba(255, 255, 255, 0.8);
    background: rgba(255, 255, 255, 0.1);
    padding: 0.25rem 0.5rem;
    border-radius: 4px;
    white-space: nowrap;
    text-align: right;
}

.status-badge {
    font-size: 0.75rem;
    padding: 0.25rem 0.5rem;
    border-radius: 12px;
    text-align: center;
    white-space: nowrap;
}

.status-badge[data-status="pendiente"] {
    background-color: rgba(255, 152, 0, 0.2);
    color: #ffa726;
}

.status-badge[data-status="pagado"] {
    background-color: rgba(76, 175, 80, 0.2);
    color: #81c784;
}

/* Ajustes para el scrolling */
.guests-list {
    max-height: 400px;
    overflow-y: auto;
    padding-right: 0.5rem;
    /* Espacio para el scrollbar */
}

.guests-list::-webkit-scrollbar {
    width: 6px;
}

.guests-list::-webkit-scrollbar-track {
    background: rgba(255, 255, 255, 0.1);
    border-radius: 3px;
}

.guests-list::-webkit-scrollbar-thumb {
    background: rgba(255, 255, 255, 0.2);
    border-radius: 3px;
}

.guests-list::-webkit-scrollbar-thumb:hover {
    background: rgba(255, 255, 255, 0.3);
}