/* Modal Styles */
.payment-modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 21000;
}

.payment-modal-content {
    background: white;
    border-radius: 12px;
    padding: 24px;
    width: 90%;
    max-width: 800px;
    max-height: 90vh;
    overflow-y: auto;
    position: relative;
}

/* Header Styles */
.payment-manager-header {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    margin-bottom: 24px;
}

.payment-manager-title {
    margin: 0;
    font-size: 1.5rem;
    font-weight: 600;
    color: #1a1a1a;
}

.payment-summary {
    font-size: 1rem;
    color: #666;
    margin-top: 4px;
}

/* Price Editing Styles */
.price-edit-container {
    margin-top: 8px;
}

.price-edit-input-container {
    display: flex;
    align-items: center;
    gap: 8px;
    background: #f8fafc;
    padding: 8px;
    border-radius: 6px;
}

.price-edit-input {
    width: 120px;
    padding: 4px 8px;
    border: 1px solid #e2e8f0;
    border-radius: 4px;
    font-size: 1rem;
}

.price-edit-icon {
    color: #64748b;
}

.price-edit-save-button {
    padding: 4px 8px;
    background: #10b981;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
}

.price-edit-button {
    background: transparent;
    border: none;
    padding: 4px;
    margin-left: 8px;
    cursor: pointer;
    color: #64748b;
}

.price-display {
    display: flex;
    flex-direction: column;
    gap: 4px;
}

.price-details {
    font-size: 0.875rem;
    color: #64748b;
}

/* History Styles */
.payment-history {
    background: #f8fafc;
    border-radius: 8px;
    padding: 16px;
    margin-bottom: 24px;
}

.payment-history-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 12px;
    background: white;
    border-radius: 6px;
    margin-bottom: 8px;
    border: 1px solid #e2e8f0;
}

.payment-history-item:last-child {
    margin-bottom: 0;
}

/* Payment Methods Grid */
.payment-methods-grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 16px;
    margin-bottom: 24px;
}

.payment-method-button {
    padding: 16px;
    border-radius: 8px;
    border: 2px solid transparent;
    transition: all 0.2s ease;
    cursor: pointer;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 8px;
}

.payment-method-icon {
    padding: 8px;
    border-radius: 50%;
    background: rgba(255, 255, 255, 0.9);
}

/* Payment Input Section */
.payment-input-section {
    background: #f8fafc;
    border-radius: 8px;
    padding: 16px;
    margin-bottom: 24px;
}

.payment-input-container {
    position: relative;
}

.payment-input {
    width: 100%;
    padding: 12px 16px 12px 40px;
    font-size: 1.125rem;
    border: 1px solid #e2e8f0;
    border-radius: 6px;
    transition: all 0.2s ease;
}

.payment-input:focus {
    outline: none;
    border-color: #3b82f6;
    box-shadow: 0 0 0 2px rgba(59, 130, 246, 0.2);
}

/* Payment Status Cards */
.payment-status {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 16px;
    margin-bottom: 24px;
}

.payment-status-card {
    padding: 16px;
    border-radius: 8px;
    display: flex;
    flex-direction: column;
}

.payment-status-card.paid {
    background: #f0fdf4;
}

.payment-status-card.pending {
    background: #fff7ed;
}

/* Action Buttons */
.payment-actions {
    display: flex;
    justify-content: flex-end;
    gap: 12px;
    margin-top: 24px;
}

.payment-button {
    padding: 10px 20px;
    border-radius: 6px;
    font-weight: 500;
    cursor: pointer;
    transition: all 0.2s ease;
}

.payment-button.save {
    background: #3b82f6;
    color: white;
    border: none;
}

.payment-button.save:hover {
    background: #2563eb;
}

.payment-button.cancel {
    background: white;
    border: 1px solid #e2e8f0;
    color: #64748b;
}

.payment-button.cancel:hover {
    background: #f8fafc;
}

/* Close Button */
.close-button {
    position: absolute;
    top: 16px;
    right: 16px;
    background: transparent;
    border: none;
    cursor: pointer;
    padding: 8px;
    color: #64748b;
    transition: color 0.2s ease;
}

.close-button:hover {
    color: #1a1a1a;
}

/* Main Payment Button */
.main-payment-button {
    width: 100%;
    padding: 12px;
    border-radius: 6px;
    border: none;
    color: white;
    cursor: pointer;
    display: flex;
    justify-content: space-between;
    align-items: center;
    transition: background-color 0.2s ease;
}

.main-payment-button.paid {
    background-color: #4CAF50;
}

.main-payment-button.pending {
    background-color: #1976d2;
}

/* Recepcionista Select */
.recepcionista-select {
    width: 100%;
    padding: 8px 12px;
    border-radius: 6px;
    border: 1px solid #e2e8f0;
    background: white;
    margin-bottom: 16px;
}

/* Status Messages */
.status-message {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 12px;
    border-radius: 6px;
    margin: 16px 0;
}

.status-message.completed {
    background: #f0fdf4;
    color: #166534;
}

.status-message.pending {
    background: #fff7ed;
    color: #9a3412;
}

/* Responsive Design */
@media (max-width: 640px) {
    .payment-methods-grid {
        grid-template-columns: 1fr;
    }

    .payment-status {
        grid-template-columns: 1fr;
    }

    .payment-modal-content {
        width: 95%;
        padding: 16px;
    }

    .payment-actions {
        flex-direction: column-reverse;
    }

    .payment-button {
        width: 100%;
    }
}