/* Styles for GridCell component */
.grid-cell {
    position: relative;
    border: 1px solid #ddd;
    transition: background-color 0.3s ease;
}

.grid-cell:hover {
    background-color: #f0f0f0;
}

.hover-info {
    position: absolute;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.7);
    color: white;
    padding: 5px;
    border-radius: 3px;
    font-size: 12px;
    pointer-events: none;
    z-index: 10;
}