.supermonth {
    display: flex;
    position: relative;
    align-items: flex-end;
    justify-content: center;
    background: none;
    border: none;
    border-radius: 0;
    box-shadow: none;
    transition: none;
    margin: 0;
    padding: 0;
    overflow: visible;
}

.lastday {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    font-size: 9px;
    box-sizing: border-box;
    height: 5vh;
    color: #FFA500;
    text-shadow: none;
    border: none;
    border-radius: 0;
    box-shadow: none;
    background: linear-gradient(to right, rgba(52, 152, 219, 0.9), rgba(41, 128, 185, 0.9));
    padding: 0;
    margin: 0;
    overflow: visible;
}